                                <div class="uploaded-file">
                                    <div class="row">
                                        <div class="col-3 thumb">
                                            <img src="../../assets/img/banner.jpg">
                                        </div>    
                                        <div class="col-7 info">
                                            <span class="bold big"> Filename-1 </span>
                                            <span class="bold"> PNG </span> <span class="size">32kb</span>
